import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'
import educationTraining from './routes/educationTraining'

const notFoundRoute: RouteRecordRaw = {
  path: '/:pathMatch(.*)*',
  name: 'not-found',
  component: async () => await import('@/views/404.vue')
}

const routes: RouteRecordRaw[] = [
  {
    path: '/',
    name: 'home',
    meta: {
      title: '',
      showFooter: true
    },
    component: async () => await import('@/views/index.vue')
  },
  {
    path: '/login',
    name: 'login',
    meta: {
      title: ''
    },
    component: async () => await import('@/views/login/Login.vue')
  },
  notFoundRoute,
  ...educationTraining
]

const router = createRouter({
  history: createWebHistory('/mincha/'),
  routes,
  scrollBehavior: () => ({ left: 0, top: 0 })
})

router.beforeEach((to) => {
  //  localStorage  login 
  const isLoggedIn = localStorage.getItem('loginData')

  //  login  /login /login
  if (!isLoggedIn && to.name !== 'login') {
    return { name: 'login' }
  }

  // 
  if (to.meta?.title && typeof to.meta.title === 'string') {
    document.title = to.meta.title
  } else if (import.meta.env.VITE_APP_NAME) {
    document.title = import.meta.env.VITE_APP_NAME
  }
})

export default router
